.wpp-container {
  background-color: #fff;
  border-radius: 10px;
  padding: 30px;
}

.wpp-container ::selection {
  color: #fff;
  background-color: #65a300;
}

.wpp-container .error {
  display: none;
}

.wpp-container .content > div {
  margin-bottom: 10px;
}

.wpp-container .content > div > p {
  margin: 0;
}

.wpp-container-payment .nav-tab-wrapper a {
  background-color: #fff;
  border-radius: 10px 10px 0 0;
}

.wpp-container .wpp-notification {
  width: -moz-fit-content;
  width: fit-content;
  color: #fff;
  background: #000;
  border-radius: 5px;
  padding: 20px 30px;
  animation: 1s show;
  position: fixed;
  top: 100px;
  right: 20px;
  box-shadow: 2px 2px 10px #595959;
}

.wpp-container .wpp-notification > .wpp-notification-title {
  font-size: 16px;
  font-weight: 600;
}

.wpp-container .wpp-notification > .wpp-notification-text {
  width: 100%;
  margin: 10px 0 0;
}

.wpp-container .wpp-notification > .wpp-notification-close {
  float: right;
  padding: 5px;
  font-size: 14px;
  font-weight: 600;
  position: relative;
  bottom: 10px;
  left: 10px;
}

.wpp-container .wpp-notification > .wpp-notification-close:hover {
  cursor: pointer;
}

.wpp-container .wpp-notification.wpp-notification-close {
  animation: 1s hide;
}

@keyframes show {
  from {
    transform: translateX(200px);
  }

  to {
    transform: translateX(0);
  }
}

@keyframes hide {
  from {
    opacity: 1;
    transform: translateX(0);
  }

  to {
    opacity: 0;
    transform: translateX(200px);
  }
}

.wpp-darker {
  z-index: 9998;
  width: 100%;
  height: 100%;
  background-color: #00000080;
  justify-content: center;
  align-items: center;
  padding: 0 30px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.wpp-darker .wpp-popup {
  height: -moz-fit-content;
  height: fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: 450px;
  background: #fff;
  border-radius: 10px;
  padding: 60px 0;
}

.wpp-darker .wpp-popup .wpp-popup-icon {
  color: #65a300;
  justify-content: center;
  align-items: center;
  font-size: 60px;
  display: flex;
}

.wpp-darker .wpp-popup .wpp-popup-icon .fa-circle-xmark {
  color: #f66;
}

.wpp-darker .wpp-popup .wpp-popup-icon .fa-circle-check {
  color: #65a300;
}

.wpp-darker .wpp-popup .wpp-popup-icon .fa-triangle-exclamation {
  color: orange;
}

.wpp-darker .wpp-popup .wpp-popup-icon .fa-spinner {
  color: #74ff33;
  animation: 2s ease-in-out infinite dash;
}

.wpp-darker .wpp-popup .wpp-popup-title {
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  font-size: 30px;
  font-weight: 600;
  display: flex;
}

.wpp-darker .wpp-popup .wpp-popup-message {
  text-align: center;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 10px;
  font-size: 16px;
  display: flex;
}

.wpp-darker .wpp-popup .wpp-popup-button {
  justify-content: center;
  align-items: center;
  margin: 20px 0;
  font-size: 18px;
  display: flex;
}

.wpp-darker .wpp-popup .wpp-popup-button .btn {
  width: -moz-fit-content;
  width: fit-content;
  height: 40px;
  border: none;
  border-radius: 5px;
  margin: 0 5px;
  padding: 10px 30px;
  font-size: 14px;
}

.wpp-darker .wpp-popup .wpp-popup-button .btn-failed {
  color: #fff;
  background: #e72e35;
}

.wpp-darker .wpp-popup .wpp-popup-button .btn-failed:hover {
  cursor: pointer;
  background: #f66;
}

.wpp-darker .wpp-popup .wpp-popup-button .btn-success {
  color: #fff;
  background: #50c6e9;
}

.wpp-darker .wpp-popup .wpp-popup-button .btn-success:hover {
  cursor: pointer;
  background: #23b7e3;
}

@media (max-width: 900px) {
  .wpp-darker {
    padding: 300px 30px 0 0;
  }
}

@keyframes dash {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

